@color: #f65c8f;
html {
    height: 100%;
    body {
        height: 100%;
        background-color: #f6f6f6;
    }
}
.title {
    padding: 0;
    text-align: center;
    background-color: @color;
    p {
        font-size: 17px;
        color: white;
        margin: 9px 0;
    }
}

.title2 {
    text-align: center;
    padding: 15px 0 0 0;
    p {
        font-size: 12px;
        color: #aaa9a9;
        font-weight: 600;
        margin: 0;
    }
}

.code {
    text-align: center;
    padding: 15px 0 0 0;
    p {
        font-size: 17px;
        color: @color;
        font-weight: 700;
        margin: 0;
        line-height: 18px;
    }
}

.title3 {
    text-align: center;
    padding: 20px 0 0 0;
    p {
        font-size: 18px;
        color: #18181a;
        font-weight: 600;
        margin: 0;
        line-height: 20px;
        letter-spacing: 2px;
    }
}

.code-img {
    text-align: center;
    padding: 10px 0 0 0;
    img {
        width: 230px;
        height: auto;
    }
}

.inputs {
    .submit-input {
        text-align: center;
        position: relative;
        width: 290px;
        margin: 10px auto;
        .submit-btn {
                
        }
    }
    
    input {
        text-align: center;
        font-size: 17px;
        color: @color;
        
    }
    input:focus {
        outline: none;
        border-color: #ccc;
    }
    input::-webkit-input-placeholder { /* WebKit browsers */
        color: #999;
        font-size: 15px;
        text-align: center;
    }
    input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        color: #999;
        font-size: 15px;
        text-align: center;
    }
    input::-moz-placeholder { /* Mozilla Firefox 19+ */
        color: #999;
        font-size: 15px;
        text-align: center;
    }
    input:-ms-input-placeholder { /* Internet Explorer 10+ */
        color: #999;
        font-size: 15px;
        text-align: center;
    }

    .submit-btn {
        height: 37px;
        width: 37px;
        border-radius: 50%;
        border: 1px solid #ccc;
        position: absolute;
        right: 2px;
        top: 2px;
        padding: 2px 2px 2px 4px;
        img {
            width: 20px;
        }
    }
    .active {
        height: 37px;
        width: 37px;
        border-radius: 50%;
        border: 1px solid @color;
        position: absolute;
        right: 2px;
        top: 2px;
        padding: 2px 2px 2px 4px;
        background-color: @color;
        img {
            width: 20px;
        }
    }
}

.foot {
    padding: 10px 20px;
    .am-vertical-align-middle {
        width: 100%;
    }
    .forgot-password {
        text-align: center;
        a {
            text-decoration: none;
            color: white;
            font-size: 14px;
        }
    }
    .third-login {
        position: relative;
        padding: 6px 0 7px 0;
        .line {
            border-top: 1px solid #bebebe;
        }
        p {
            position: absolute;
            font-size: 12px;
            color: #bebebe;
            margin: 0;
            line-height: 14px;
            top: 0;
            left: 50%;
            margin-left: -55px;
            width: 110px;
            background-color: #f6f6f6;
            text-align: center;
        }
        margin-bottom: 1.6rem;
    }
    .login-link {
        .qq {
            float: left;
            width: 50%;
            text-align: center;
            img {
                width: 40px;
            }
        }
        .weibo {
            .qq
        }
    }
}